import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import GamesMenu from './components/GamesMenu.vue'
import PGame from './components/PGame.vue'
import Words from './components/Words.vue'
import MiniTest from './components/MiniTest.vue'
import EditWordlist from './components/EditWordlist.vue'
import EliminationGame from './components/EliminationGame.vue'

Vue.use(VueRouter);

const routes = [
    { path: '/', component: GamesMenu },
    { path: '/pgame', name: 'pgame', component: PGame, props: true },
    { path: '/words/:wordlistid', name: 'words', component: Words, props: true },
    { path: '/test/:wordlistid', name: 'test', component: MiniTest, props: true },
    { path: '/edit/:wordlistid', name: 'edit', component: EditWordlist, props: true },
    { path: '/elimination/:wordlistid', name: 'elimination', component: EliminationGame, props: true }
]

const router = new VueRouter({
    routes
})

import firebase from 'firebase';
import 'firebase/firestore';

