<template>
    <div class="center">
        <h1>Test - {{ questionIndex + 1 }} of {{ questions.length }} [{{ score }}%]</h1>
        <img :src="answer.img" :class="status"/>
        <ul v-if="!isCorrect">
            <li v-for="answer in getAnswers" :key="answer" @click="checkAnswer(answer)">{{ answer  }}</li>
        </ul>
        <h3 v-if="isNext">{{ answer.name }} - {{ answer.native }}</h3>
        <button class="next" v-if="isNext && !isFinished" @click="nextQuestion">Next Question &gt;&gt;</button>
        <button class="restart" v-if="isNext && isFinished" @click="startAgain">Start Again</button>
    </div>
</template>

<script>
import firebase from 'firebase';
var statusTypes = { correct: 'correct', wrong: 'wrong', none: '' }

export default {
    props: [ 'wordlistid' ],
    data() {
        return {
            answer: {},
            wrongAnswers: [],
            status: '',
            questionIndex: 0,
            questions: [],
            answered: 0,
            answeredCorrect: 0
        }
    },
    mounted() {
        window.console.log("id: " + this.wordlistid);
        this.init();
    },
    computed: {
        getAnswers() {
            window.console.log("called get answers");
            if (this.wrongAnswers.length > 0) {
                return this.shuffle([
                    this.answer.name,
                    this.wrongAnswers[0].name,
                    this.wrongAnswers[1].name,
                    this.wrongAnswers[2].name]);
            } else {
                return [];
            }
        },
        isCorrect() {
            return this.status === statusTypes.correct;
        },
        isNext() {
            return this.isCorrect;
        },
        isFinished() {
            return (this.questionIndex + 1) == this.questions.length;
        },
        score() {
            return this.answered > 0 ? parseInt(this.answeredCorrect / this.answered * 100): 0;
        }
    },
    methods: {
        init() {
            var db = firebase.firestore();
            db.collection('wordlist').doc(this.wordlistid).get()
                .then(doc => {
                    if (!doc.exists) {
                        window.console.log("No such document");
                    } else {
                        window.console.log("Data:");
                        window.console.log(doc.data());

                        for (var item of doc.data().items) {
                            this.questions.push(item);
                        }
                        this.shuffle(this.questions);
                        this.newQuestion();
                    }
                })
                .catch(err => {
                    window.console.log('Error', err);
                });
        },
        newQuestion() {
            this.answer = this.questions[this.questionIndex];

            var wrongIndices = [];
            this.wrongAnswers = [];
            for (var index = 0; index < 3; index++) {
                var randomIndex = -1;
                while (randomIndex == -1) {
                    randomIndex = parseInt(Math.random() * this.questions.length);
                    if (randomIndex === this.questionIndex) {
                        randomIndex = -1;
                    } else {
                        if (wrongIndices.includes(randomIndex)) {
                            randomIndex = -1;
                        }
                    }
                }
                wrongIndices.push(randomIndex);
            }

            for (let wrongIndex of wrongIndices) {
                this.wrongAnswers.push(this.questions[wrongIndex]);
            }
        },
        startAgain() {
            this.answer = {};
            this.wrongAnswers = [];
            this.status = '';
            this.questionIndex = 0;
            this.questions = [];
            this.answered = 0;
            this.answeredCorrect = 0;

            this.init();
        },
        shuffle(arra1) {
            var ctr = arra1.length, temp, index;

            // While there are elements in the array
            while (ctr > 0) {
                // Pick a random index
                index = Math.floor(Math.random() * ctr);
                // Decrease ctr by 1
                ctr--;
                // And swap the last element with it
                temp = arra1[ctr];
                arra1[ctr] = arra1[index];
                arra1[index] = temp;
            }
            window.console.log(arra1);
            return arra1;
        },
        checkAnswer(answerAttempt) {
            this.answered++;
            if (this.status !== statusTypes.correct) {
                if (answerAttempt == this.answer.name) {
                    this.status = statusTypes.correct;
                    this.answeredCorrect++;
                } else {
                    this.status = statusTypes.wrong;
                }
            }
        },
        nextQuestion() {
            this.questionIndex++;
            this.newQuestion();
            this.status = statusTypes.none;
        }
    }
}
</script>

