<template>
    <div class="center">
        <h1>{{ wordlist.name }}</h1>
        <PictureBloc v-for="item in wordlist.items" :item="item" :key="item.name"/>
    </div>
</template>

<script>
import PictureBloc from './PictureBloc';
import firebase from 'firebase';

export default {
    props: [ 'wordlistid' ],
    data() {
        return {
            wordlist: []
        }
    },
    components: { PictureBloc },
    mounted() {
        window.console.log("id: " + this.wordlistid);

        var db = firebase.firestore();
        db.collection('wordlist').doc(this.wordlistid).get()
            .then(doc => {
                if (!doc.exists) {
                    window.console.log("No such document");
                } else {
                    window.console.log("Data:");
                    window.console.log(doc.data());
                    this.wordlist = doc.data();
                }
            })
            .catch(err => {
                window.console.log('Error', err);
            });
    }
}
</script>

