<template>
    <div class="center" id="menu">
        <h1>Categories</h1>
        <WordlistIcon
            v-for="wordlist in wordlists"
            :key="wordlist.index"
            :wordlist="wordlist"
            :class="selected(wordlist) ? 'selected' : ''"
            @click="chooseWordlist(wordlist)"
        />
        <div class="actions" v-if="chosenWordlist !== ''">
            <button class="words" @click="openWords">Word Review</button>
            <button class="test" @click="openTest">Mini Test</button>
            <button class="game" @click="openGame">Elimination Game</button>
            <button class="edit" @click="openEdit">Edit</button>
        </div>
    </div>
</template>

<script>
import WordlistIcon from './WordlistIcon.vue';
import firebase from 'firebase';

export default {
    components: {
        WordlistIcon,
    },
    data() {
        return {
            chosenWordlist: '',
            wordlists: []
        }
    },
    mounted() {
        var db = firebase.firestore();
        db.collection('wordlists').orderBy('index', 'asc').get()
            .then(snapshot => {
                snapshot.forEach(doc => {
                    this.wordlists.push(doc.data());
                })
            })
            .catch(err => {
                window.console.log('Error', err);
            });
    },
    methods: {
        openPictureGame() {
            this.$router.push({name: 'pgame', params: {themeid:this.chosenWordlist.id}});
        },
        openWords() {
            this.$router.push('/words/' + this.chosenWordlist.id);
        },
        openTest() {
            this.$router.push('/test/' + this.chosenWordlist.id);
        },
        openEdit() {
            this.$router.push('/edit/' + this.chosenWordlist.id);
        },
        openGame() {
            this.$router.push('/elimination/' + this.chosenWordlist.id);
        },
        chooseWordlist(wordlist) {
            window.console.log("clicked");
            this.chosenWordlist = wordlist;
        },
        selected(wordlist) {
            return this.chosenWordlist.name === wordlist.name;
        }
    }
}
</script>
