<template>
    <div class="center">
        <h1>Edit</h1>
        <ul>
            <li v-for="(word, index) in wordlist" :key="word.name">
                <span class="index">{{ index + 1 }}</span>
                <input type="text" class="name" v-model="word.name" placeholder="English" />
                <input type="text" class="native" v-model="word.native" placeholder="Korean" />
                <textarea v-model="word.img"></textarea>
                <img :src="word.img" />
            </li>
        </ul>
        <button @click="addWord">+</button>
        <button @click="saveWords">Save</button>
    </div>
</template>

<script>
import firebase from 'firebase';

export default {
    props: [ 'wordlistid' ],
    data() {
        return {
            wordlist: []
        }
    },
    mounted() {
        window.console.log("id: " + this.wordlistid);

        var db = firebase.firestore();
        db.collection('wordlist')
            .doc(this.wordlistid)
            .get()
            .then(doc => {
                if (!doc.exists) {
                    window.console.log("No such document");
                } else {
                    window.console.log("Data:");
                    window.console.log(doc.data());
                    this.wordlist = doc.data().items;
                }
            })
            .catch(err => {
                window.console.log('Error', err);
            });
    },
    methods: {
        addWord() {
            this.wordlist.push({name: "", img: ""})
        },
        saveWords() {
            var db = firebase.firestore();
            db.collection('wordlist')
                .doc(this.wordlistid)
                .set({items: this.wordlist});
        }
    }
}
</script>

<style lang="scss" scoped>
div.center {
    width: 1600px;
    margin: 0 auto;

    @media (max-width: 1600px) {
        width: 100%;
    }
}

ul {
    list-style: none;
    padding-left: 0;

    li {

        margin-bottom: 10px;

        .index {
            font-size: 40px;
            color: #aaa;
            padding-left: 10px;
            padding-right: 10px;
            border: 1px solid#aaa;
            margin-left: 10px;
            vertical-align: middle;
            height: 40px;
        }

        .name, .native {
            font-size: 20px;
            padding: 5px;
            vertical-align: middle;
            height: 40px;
            margin-left: 10px;
        }
        

        textarea {
            vertical-align: middle;
            width: 600px;
            margin-left: 10px;
        }

        img {
            height: 55px;
            vertical-align: middle;
            margin-left: 10px;
        }
    }
}

button {
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 5px;
    margin-left: 10px;
    font-size: 40px;
    color: #555;
    cursor: pointer;
}
</style>