<template>
    <div class="center">
        <span class="box" v-for="(item, index) in wordlist.items" :key="item.name">
            <img :src="item.img" @click="clickImage(item)" :class="[ item.eliminated ? 'eliminated' : '' ]" />
            <span class="index">{{ index }}</span>
        </span>
    </div>
</template>

<script>
import firebase from 'firebase';

export default {
    props: [ 'wordlistid' ],
    data() {
        return {
            wordlist: []
        }
    },
    mounted() {
        window.console.log("id: " + this.wordlistid);

        var db = firebase.firestore();
        db.collection('wordlist').doc(this.wordlistid).get()
            .then(doc => {
                if (!doc.exists) {
                    window.console.log("No such document");
                } else {
                    window.console.log("Data:");
                    window.console.log(doc.data());
                    this.wordlist = doc.data();
                }
            })
            .catch(err => {
                window.console.log('Error', err);
            });
    },
    methods: {
        clickImage(image) {
            if (!image.eliminated) {
                this.$set(image, "eliminated", true);
            } else {
                image.eliminated = !image.eliminated;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
div.center {
    width: 1600px;
    margin: 0 auto;

    @media (max-width: 1600px) {
        width: 100%;
    }
}


img {
    width: 200px;
    padding: 10px;
    margin: 5px;
    vertical-align: top;
    border: 3px solid #8682ee;

    &:hover {
        cursor: pointer;
        background-color: #d9d8ff;
    }

    &.eliminated {
        background-color: red;
        filter: grayscale(80%);
    }  
}

.index {
    position: absolute;
    margin-left: -228px;
    margin-top: 8px;
    background-color: white;
    padding: 6px;
    font-size: 22px;
    border: 3px solid black;
}
</style>