<template>
    <div class="wordlist" @click="$emit('click')">
        <img :src="wordlist.img"/>
        <p>{{ wordlist.name }}</p>
    </div>
</template>

<script>
export default {
    props: [ 'wordlist' ]
}
</script>

<style lang="scss" scoped>
.wordlist {
    border: 1px solid black;
    display: inline-block;
    padding: 10px;

    &:hover {
        background-color: #eee;
        cursor: pointer;
    }

    &.selected {
        background-color: #a2a2f0;
    }

    img {
        width: 100px;
        height: 100px;
    }
}
</style>